<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.vin" placeholder="vin"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getDevices"></el-button>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		
		<!---->
		<el-table :data="devices" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="vin" label="vin" width="180" sortable>
			</el-table-column>
			<el-table-column prop="name" label="" width="180" sortable>
			</el-table-column>
			<el-table-column prop="uploaddate" label="" width="260" sortable>
			</el-table-column>
			<el-table-column prop="uploadtimes" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="devicecert" label="" width="120" :formatter="formatCert" sortable>
			</el-table-column>
			<el-table-column prop="deviceauth" label="" width="120" :formatter="formatAuth" sortable>
			</el-table-column>
			<el-table-column prop="download_deviceauth" label="" width="120" :formatter="formatDownload" sortable>
			</el-table-column>
			<el-table-column label="" width="200">
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="primary" @click="handleBatch"></el-button>
			<el-button type="primary" @click="handleDownload"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		
		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" ref="editForm">
				<el-form-item label="">
					<el-radio-group v-model="editForm.deviceauth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
		
		<!---->
		<el-dialog title="" v-model="batchFormVisible" :close-on-click-modal="false">
			<el-form :model="batchForm" label-width="80px" ref="batchForm">
				<el-form-item label="">
					<el-radio-group v-model="batchForm.trafficcontrol">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="batchFormVisible = false"></el-button>
				<el-button type="primary" @click.native="batchSubmit" :loading="batchLoading"></el-button>
			</div>
		</el-dialog>
		
		<!---->
		<el-dialog title="" v-model="downloadFormVisible" :close-on-click-modal="false">
			<el-form :model="downloadForm" label-width="80px" ref="downloadForm">
				<el-form-item label="">
					<el-radio-group v-model="downloadForm.downloadcontrol">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="downloadFormVisible = false"></el-button>
				<el-button type="primary" @click.native="downloadSubmit" :loading="downloadLoading"></el-button>
			</div>
		</el-dialog>
		
		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="vin" prop="vin">
					<el-input v-model="addForm.vin" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="datetime" placeholder="" v-model="addForm.uploaddate"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.uploadtimes" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.devicecert">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.deviceauth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.download_deviceauth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
		
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getDeviceListPage, editDevice, trafficControl, downloadControl, addDevice, removeDevice } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: '',
					vin: ''
				},
				devices: [],
				total: 0,
				page: 1,
				listLoading: false,
				
				//
				editFormVisible: false,
				editLoading: false,
				editForm: {
					id: 0,
					deviceauth: -1
				},
				//
				batchFormVisible: false,
				batchLoading: false,
				batchForm: {
					trafficcontrol: -1
				},
				//
				downloadFormVisible: false,
				downloadLoading: false,
				downloadForm: {
					downloadcontrol: -1
				},
				//
				addFormVisible: false, //
				addLoading: false,
				addFormRules: {
					vin: [
						{ required: true, message: 'vin', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
				},
				addForm: {
					name: '',
					vin: '',
					uploaddate: '',
					uploadtimes: 0,
					devicecert: -1,
					deviceauth: -1,
					download_deviceauth: -1,
				},
			}
		},
		
		methods: {
			//
			formatCert: function (row, column) {
				return row.devicecert == 1 ? '' : row.devicecert == 0 ? '' : '';
			},
			//
			formatAuth: function (row, colomn) {
				return row.deviceauth == 1 ? '' : row.deviceauth == 0 ? '' : '';
			},
			//
			formatDownload: function (row, colomn) {
				return row.download_deviceauth== 1 ? '' : row.download_deviceauth == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getDevices();
			},
			//
			getDevices() {
				let para = {
					page: this.page,
					name: this.filters.name,
					vin: this.filters.vin
				};
				console.log(para)
				this.listLoading = true;
				//NProgress.start();
				getDeviceListPage(para).then((res) => {
					console.log('device')
					console.log(res)
					this.total = res.data.total;
					this.devices = res.data.devices;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
				console.log("he")
				console.log(this.editForm)
				console.log(this.editFormVisible)
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							editDevice(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getDevices();
							});
						});
					}
				});
			},
			//
			handleBatch: function () {
				this.batchFormVisible = true;
			},
			//
			batchSubmit: function () {
				this.$refs.batchForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.batchLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.batchForm);
							console.log("liuliangkongzhi");
							console.log(para);
							trafficControl(para).then((res) => {
								this.batchLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['batchForm'].resetFields();
								this.batchFormVisible = false;
								this.getDevices();
							});
						});
					}
				});
			},
			//
			handleDownload: function () {
				this.downloadFormVisible = true;
			},
			//
			downloadSubmit: function () {
				this.$refs.downloadForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.downloadLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.downloadForm);
							console.log("xiazaiquanxian");
							console.log(para);
							downloadControl(para).then((res) => {
								console.log(res.data)
								this.downloadLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['downloadForm'].resetFields();
								this.downloadFormVisible = false;
								this.getDevices();
							});
						});
					}
				});
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					vin: '',
					uploaddate: '',
					uploadtimes: 0,
					devicecert: -1,
					deviceauth: -1,
					download_deviceauth: -1,
				};
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							console.log(para)
							para.uploaddate = (!para.uploaddate || para.uploaddate == '') ? '' : util.formatDate.format(new Date(para.uploaddate), 'yyyy-MM-dd hh:mm:ss');
							addDevice(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getDevices();
							});
						});
					}
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { vin: row.vin };
					console.log(para)
					removeDevice(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getDevices();
					});
				}).catch(() => {
			
				});
			},
		},
		
		mounted() {
			this.getDevices();
		}
	}

</script>

<style scoped>

</style>


